@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@200;300;400;500;600;700;800&display=swap");
*{
    font-family: "Roboto Condensed",sans-serif;
}

body{
    margin: 0;
    padding: 0;
    background-color: #2f3640;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.App{
    width: 350px;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
.searchBox{
    width:90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid skyblue;
    padding: 5px 8px;
    border-radius: 5px;
}
.inputSearch{
    border: none;
    outline: none;
    font-size: 16px;
    width: 100%;
}
.searchIcons{
    color: blue;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    transition: 100ms;
}
.searchIcons:hover{
    font-size: 22px;
    transform: scale(1);
}