.img{

}

.img-icons{
    width: 150px;
    height: 150px;
    margin-left: 100px;
}

.temp,.city,.country{
    text-align:center;
}
.temp{
    font-weight: bold;
    font-size: 26px;
}
.city{
    color: #ffbc00;
    font-size: 40px;
    font-weight: 500;
}
.country{
    color: #888;
    font-size: 18px;
    font-weight: bold;
}
.cord{
    display: flex;
    justify-content: center;
    align-items: center;
}
.cord div{
   display: flex;
   justify-content: center;
   flex-direction: column;
   align-items: center;
   padding: 10px;
}
.cord div span{
    font-size: 14px;
    font-weight: bold;
    color: gray;
}

.cord div span:nth-child(2){
    font-size: 20px;
    color: blue;
}
.data-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 10px 0 10px;
}
.windIcons{
    font-size: 35px;
    color: gray;
}
.presentage{
    font-weight: bold;
    font-size: 20px;
}
.NameOfFeild{
    color: gray;
    font-size: 14px;
}